import { Controller } from '@ali/4ever-cangjie';
;

export default function isBlockquoteDisabled(controller: Controller) {
  const { selection } = controller.value;
  const { isCollapsedAtEdge } = selection;
  if (isCollapsedAtEdge) {
    return true;
  }
  if (controller.query('isSelectionInListSymbol')) {
    return true;
  }
  return false;
}
